
import Vue from "vue";
import { getAllJiraField } from "@/apis/jiraInfo";
import { getAllJiraIssue } from "@/apis/jiraInfo";
import { getJiraInfoFake } from "@/apis/jiraInfo";

interface PriorityData {
  [index: string]: {
    color: string;
    icon: string;
    sortPriority: number;
  };
}

interface TaskTypeData {
  [index: string]: {
    color: string;
    icon: string;
    sortPriority: number;
  };
}

interface StatusData {
  [index: string]: {
    color: string;
    sortPriority: number;
  };
}

export default Vue.extend({
  data () {
    return {
      headerTooltips: {
        type: 'Main  Sub',
        status: 'In Progress  Done',
        priority: 'Highest  Lowest',
        default: 'A  Z'
      },
      priorityData: {
        'Highest': {
          color: 'deep-orange darken-1',
          icon: 'mdi-chevron-double-up',
          sortPriority: 5
        },
        'High': {
          color: 'deep-orange lighten-1',
          icon: 'mdi-chevron-up',
          sortPriority: 4
        },
        'Medium': {
          color: 'yellow darken-2',
          icon: 'mdi-approximately-equal',
          sortPriority: 3
        },
        'Low': {
          color: 'blue darken-1',
          icon: 'mdi-chevron-down',
          sortPriority: 2
        },
        'Lowest': {
          color: 'blue darken-3',
          icon: 'mdi-chevron-double-down',
          sortPriority: 1
        }
      } as PriorityData,
      taskTypeData:{
        'Task': {
          color: 'red',
          icon: 'mdi-file-document',
          sortPriority: 2
        },
        'Subtask': {
          color: 'blue',
          icon: 'mdi-file-tree',
          sortPriority: 1
        }
      } as TaskTypeData,
      statusData: {
        'In Progress': {
          color: 'blue lighten-4 blue--text text--darken-4',
          sortPriority: 3
        },
        'To Do': {
          color: 'grey lighten-2 grey--text text--darken-3',
          sortPriority: 2
        },
        'Done': {
          color: 'green lighten-4 green--text text--darken-4',
          sortPriority: 1
        }
      } as StatusData,
      headers: [] as object[],
      selectedField:[] as any[],
      issueData: [] as object[],
      dtSearch: '',
      cbSearch: '',
      loading: true
    };
  },
  methods: {
    setFieldAlign() {
      this.cbSearch='';
      this.headers.forEach((item: any, index: number) => {
        if(this.selectedField.find((field)=> field.value == item.value) || item.disabled)
          item.align = 'start';
        else
          item.align = ' d-none';
      });
    },
    getJiraField() {
      this.headers = getAllJiraField();
      // getAllJiraField()
      //   .then(response => {
      //   })
      //   .catch(err => {
      //     alert("");
      //   });
    },
    getJiraIssue() {
      // this.issueData = getJiraInfoFake();
      this.loading = false;
      getAllJiraIssue(Number(this.$route.params.repoId))
        .then(response => {
          this.loading = true;
          response.data.result.forEach((item: any) => {
            this.issueData.push({
              key: item.key,
              summary: item.summary,
              type: item.type,
              priority: item.priority,
              status: item.status,
              resolution: item.resolution,
              label: item.label.join(', '),
              created: new Date(item.created).toLocaleString("zh-tw"),
              updated: new Date(item.updated).toLocaleString("zh-tw")
            });
          });
          this.loading = false;
        })
        .catch(err => {
          alert("");
        });
    },
    customSort(items: any[], indexes: string[], isDescending: boolean[]) {
      items.sort((a: any, b: any) => {
        let p = 0;
        for (let i = 0; i < indexes.length; i++) {
          const index = indexes[i];
          const isDesc = isDescending[i];
          if (index === "priority") {
            p = this.priorityData[b.priority].sortPriority - this.priorityData[a.priority].sortPriority;
          }
          else if (index === "status") {
            p = this.statusData[b.status].sortPriority - this.statusData[a.status].sortPriority;
          }
          else if (index === "type") {
            p = this.taskTypeData[b.type].sortPriority - this.taskTypeData[a.type].sortPriority;
          }
          else if (typeof a[index] === "string") {
            p = a[index].toLowerCase().localeCompare(b[index].toLowerCase());
          }
          else if (typeof a[index] === "number") {
            p = b[index] - a[index];
          }
          else {
            p = 0;
          }
          if (isDesc) p = -p;
          if (p) break;
        }
        return p;
      });
      return items;
    }
  },
  mounted() {
    this.getJiraIssue();
    this.getJiraField();
    this.setFieldAlign();
  }
});
