
import store from "@/store";
import Vue from "vue";
import Notification from "@/components/Notification.vue";

export default Vue.extend({
  components: {
    Notification
  },
  computed: {
    isAuth() {
      return store.auth.isAuthenticated;
    }
  },
  methods: {
    logout: () => {
      store.auth.logout();
    }
  }
});
