
import Vue from "vue";
import router from "@/router";
import { bindTrelloInfoPost, clearTrelloBindInfo, checkTrelloKeyToken} from "@/apis/repository";
export default Vue.extend({
  props: [],
  data() {
    return {
      trelloData:{
        key: "",
        token: "",
      },
      dialog: false,
      trelloBindStatus: false,
    };
  },
  watch: {
  },
  mounted() {
    this.checkBindStatus();
  },
  methods: {
    clearInputData() {
      this.trelloData.key = "",
      this.trelloData.token = ""
    },
    // trello
    async checkBindStatus() {
      const result = await checkTrelloKeyToken();
      if (result.data.success) { 
        this.trelloBindStatus =  true;
      }
      else {
        this.trelloBindStatus = false;
      }
    },
    // trello
    async bindTrelloInfo() {
        const result = await bindTrelloInfoPost(
            this.trelloData.key,
            this.trelloData.token
        );
        if (result.data.success) {
            this.$emit(
                "msgSnackBar",
                true,
                "Bind Success!"
            );
            this.dialog = false;
        } 
        else {
            this.$emit(
                "msgSnackBar",
                false,
                "Bind Fail!"
            );
            this.dialog = false;
        }
        this.checkBindStatus();
    },
    // trello
    async clearTrelloInfo() {
        const result = await clearTrelloBindInfo();
        if (result.data.success) {
            this.$emit(
                "msgSnackBar",
                true,
                "Clear Success!"
            );
            this.dialog = false;
        } 
        else {
            this.$emit(
                "msgSnackBar",
                false,
                "Clear Fail!"
            );
            this.dialog = false;
        }
        this.checkBindStatus();
    }
  },
});
