
import Vue from "vue";
export default Vue.extend({
  props: ["vCardTitle", "vTextLabel", "userInfo"],
  data() {
    return {
      inputData: "",
      dialog: false
    };
  },
  methods: {
    add() {
      this.$emit("invite", this.inputData);
      this.dialog = false;
    },
    clearInputData() {
      this.$emit("clickInvitation", this.inputData);
      this.inputData = "";
    }
  }
});
