
import Vue from "vue";
import {
  getProjects,
  editProjectNameByAdmin,
  deleteProjectByAdmin
} from "@/apis/projects.ts";

export default Vue.extend({
  props: ["projects"],
  data: () => {
    return {
      search: "",
      dialog: false,
      dialogDelete: false,
      headers: [
        {
          text: "Id",
          align: "start",
          value: "id"
        },
        { text: "Name", value: "name" },
        { text: "Owner", value: "ownerName" },
        { text: "Members", value: "members" },
        { text: "Actions", value: "actions", sortable: false }
      ],
      editedIndex: -1,
      editedItem: {
        name: ""
      },
      defaultItem: {
        name: ""
      }
    };
  },
  computed: {
    formTitle(): string {
      return this.editedIndex === -1 ? "New Project" : "Edit Project";
    }
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    }
  },
  methods: {
    editItem(item: any) {
      this.editedIndex = this.projects.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item: any) {
      this.editedIndex = this.projects.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    async deleteItemConfirm() {
      const response = (
        await deleteProjectByAdmin(this.projects[this.editedIndex].id)
      )["data"];
      if (response.success) this.projects.splice(this.editedIndex, 1);
      this.$emit("update");
      this.$emit("showMessage", response);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        const newProjectInfo = [
          {
            value: this.editedItem.name,
            path: "/name",
            op: "replace"
          }
        ];
        Object.assign(this.projects[this.editedIndex], this.editedItem);
        const project = this.projects[this.editedIndex];
        editProjectNameByAdmin(project.id, newProjectInfo)
          .then(response => {
            if (response.data.success) {
              this.$emit("showMessage", response.data);
              this.$emit("update");
            }
          })
          .catch(err => {
            this.$emit("showMessage", err);
            this.$emit("update");
          });
      } else {
        this.projects.push(this.editedItem);
      }
      this.close();
    }
  }
});
