
import Vue from "vue";

export default Vue.extend({
  props: {
    name: { type: String },
    avatarUrl: { type: String }
  },
  data() {
    return {
      userName: this.name,
      max25chars: function(v: any) {
        return v.length <= 25 || "Input too long!";
      }
    };
  },
  methods: {
    save() {
      this.$emit("save", this.userName);
    }
  },
  watch: {
    name(newVal) {
      this.userName = newVal;
    }
  }
});
